<template>
  <div id="sidebar">
    <avatar/>
    <div class="icons">
      <router-link to="/note/1" title="笔记">
        <i class="iconfont icon-note"/>
      </router-link>
      <router-link to="/notebooks" title="笔记本">
        <i class="iconfont icon-notebook"/>
      </router-link>
      <router-link to="/trash/2" title="回收站">
        <i class="iconfont icon-trash"/>
      </router-link>
    </div>
    <div class="logout" @click="logout">
      <i class="iconfont icon-logout"/>
    </div>
  </div>
</template>

<script>
import avatar from '@/components/Avatar';
import Auth from '@/apis/auth';

export default {
  components: {
    avatar
  },
  methods: {
    logout() {
      console.log('logout');
      Auth.logout().then(data => {
        console.log(data);
      });
    }
  }
};

</script>

<style lang="less" scoped>
#sidebar {
  position: relative;
  width: 56px;
  text-align: center;
  background-color: #2c333c;

  .icons {
    margin-top: 15px;

    a {
      padding: 6px 0;
      display: block;
    }

    .router-link-active {
      background-color: #5e6266;
    }
  }

  .logout {
    position: absolute;
    bottom: 20px;
    width: 100%;
    text-align: center;
    cursor: pointer;
  }

  .iconfont {
    color: #fff;
  }
}
</style>



